import React from 'react';
import './Services.css';

const Services = (props) => {
    // console.log(props.service);
    const { name, img, price, stock } = props.service;
    return (
        <div>
            <h1>{name}</h1>
            <img src={img} alt="" />
            <h1>{price}</h1>
            <h1>{stock}</h1>
        </div>
    );
};

export default Services;